import React, { useRef, useEffect, useMemo } from 'react';
import {
  Box,
  Paper,
  Typography,
} from '@mui/material';
import { RawFrequencyData } from '../types';
import { SWARA_NOTES } from '../utils/audioProcessing';

interface WaveformVisualizationProps {
  rawFrequencies: RawFrequencyData[];
  tonicFrequency: number;
  isLive?: boolean;
  currentTime?: number;
  duration?: number;
}

const WaveformVisualization: React.FC<WaveformVisualizationProps> = ({
  rawFrequencies,
  tonicFrequency,
  isLive = false,
  currentTime = 0,
  duration = 0,
}) => {
  const canvasRef = useRef<HTMLCanvasElement>(null);

  // Calculate swara frequencies based on tonic
  const swaraFrequencies = useMemo(() => {
    const frequencies: { [key: string]: number } = {};
    SWARA_NOTES.forEach(swara => {
      frequencies[swara.symbol] = tonicFrequency * Math.pow(2, swara.semitones / 12);
    });
    return frequencies;
  }, [tonicFrequency]);

  // Prepare data points: frequency vs time
  const dataPoints = useMemo(() => {
    if (rawFrequencies.length === 0) return [];
    
    // For live mode, show last 10 seconds relative to the most recent frequency
    // For file mode, use timestamps as-is (they're already relative to start)
    let processedFrequencies = rawFrequencies;
    
    if (isLive && rawFrequencies.length > 0) {
      const mostRecentTime = rawFrequencies[rawFrequencies.length - 1].timestamp;
      const cutoffTime = mostRecentTime - 10000; // 10 seconds ago
      processedFrequencies = rawFrequencies.filter(freq => freq.timestamp >= cutoffTime);
      
      // Normalize timestamps to be relative to the oldest frequency in the window
      if (processedFrequencies.length > 0) {
        const baseTime = processedFrequencies[0].timestamp;
        processedFrequencies = processedFrequencies.map(freq => ({
          ...freq,
          timestamp: freq.timestamp - baseTime
        }));
      }
    }
    
    // Filter out silence (frequency = 0) and map to data points
    return processedFrequencies
      .filter(freq => freq.frequency > 0) // Remove silence
      .map(freq => ({
        time: freq.timestamp,
        frequency: freq.frequency,
      }));
  }, [rawFrequencies, isLive]);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;
    
    if (dataPoints.length === 0) {
      // Clear canvas if no data
      const ctx = canvas.getContext('2d');
      if (ctx) {
        ctx.fillStyle = '#f5f5f5';
        ctx.fillRect(0, 0, canvas.width, canvas.height);
      }
      return;
    }

    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    const width = canvas.width;
    const height = canvas.height;
    const padding = { top: 40, right: 60, bottom: 40, left: 60 };
    const plotWidth = width - padding.left - padding.right;
    const plotHeight = height - padding.top - padding.bottom;

    // Clear canvas
    ctx.fillStyle = '#ffffff';
    ctx.fillRect(0, 0, width, height);

    // Calculate time and frequency ranges
    const times = dataPoints.map(p => p.time);
    const frequencies = dataPoints.map(p => p.frequency);
    const minTime = Math.min(...times);
    const maxTime = Math.max(...times);
    const timeRange = maxTime - minTime || 1;
    
    const minFreq = Math.max(80, Math.min(...frequencies) * 0.9);
    const maxFreq = Math.min(2000, Math.max(...frequencies) * 1.1);
    const freqRange = maxFreq - minFreq || 1;

    // Draw reference lines for swara frequencies
    ctx.strokeStyle = '#e0e0e0';
    ctx.lineWidth = 1;
    ctx.setLineDash([5, 5]);
    Object.entries(swaraFrequencies).forEach(([symbol, freq]) => {
      if (freq >= minFreq && freq <= maxFreq) {
        const y = padding.top + plotHeight - ((freq - minFreq) / freqRange) * plotHeight;
        ctx.beginPath();
        ctx.moveTo(padding.left, y);
        ctx.lineTo(padding.left + plotWidth, y);
        ctx.stroke();
        
        // Label the swara
        ctx.fillStyle = '#999';
        ctx.font = '10px Arial';
        ctx.textAlign = 'right';
        ctx.fillText(symbol, padding.left - 5, y + 3);
      }
    });
    ctx.setLineDash([]);

    // Draw axes
    ctx.strokeStyle = '#333';
    ctx.lineWidth = 2;
    ctx.beginPath();
    ctx.moveTo(padding.left, padding.top);
    ctx.lineTo(padding.left, padding.top + plotHeight);
    ctx.lineTo(padding.left + plotWidth, padding.top + plotHeight);
    ctx.stroke();

    // Draw axis labels
    ctx.fillStyle = '#333';
    ctx.font = '12px Arial';
    ctx.textAlign = 'center';
    ctx.fillText('Time (ms)', padding.left + plotWidth / 2, height - 10);
    
    ctx.save();
    ctx.translate(15, padding.top + plotHeight / 2);
    ctx.rotate(-Math.PI / 2);
    ctx.textAlign = 'center';
    ctx.fillText('Frequency (Hz)', 0, 0);
    ctx.restore();

    // Draw frequency tick marks
    const freqTicks = [100, 200, 300, 400, 500, 600, 700, 800, 900, 1000, 1500, 2000];
    freqTicks.forEach(freq => {
      if (freq >= minFreq && freq <= maxFreq) {
        const y = padding.top + plotHeight - ((freq - minFreq) / freqRange) * plotHeight;
        ctx.strokeStyle = '#ccc';
        ctx.lineWidth = 1;
        ctx.beginPath();
        ctx.moveTo(padding.left, y);
        ctx.lineTo(padding.left - 5, y);
        ctx.stroke();
        
        ctx.fillStyle = '#666';
        ctx.font = '9px Arial';
        ctx.textAlign = 'right';
        ctx.fillText(freq.toString(), padding.left - 8, y + 3);
      }
    });

    // Draw the continuous frequency curve (like gen2.py)
    if (dataPoints.length > 0) {
      ctx.strokeStyle = '#1976d2';
      ctx.lineWidth = 1.5;
      ctx.beginPath();
      
      let isFirstPoint = true;
      dataPoints.forEach((point) => {
        const x = padding.left + ((point.time - minTime) / timeRange) * plotWidth;
        const y = padding.top + plotHeight - ((point.frequency - minFreq) / freqRange) * plotHeight;
        
        if (isFirstPoint) {
          ctx.moveTo(x, y);
          isFirstPoint = false;
        } else {
          ctx.lineTo(x, y);
        }
      });
      
      ctx.stroke();
    }

    // Draw current time indicator for file playback
    if (!isLive && duration > 0 && maxTime > 0) {
      const timeMs = currentTime * 1000;
      if (timeMs >= minTime && timeMs <= maxTime) {
        const x = padding.left + ((timeMs - minTime) / timeRange) * plotWidth;
        ctx.strokeStyle = '#dc004e';
        ctx.lineWidth = 2;
        ctx.beginPath();
        ctx.moveTo(x, padding.top);
        ctx.lineTo(x, padding.top + plotHeight);
        ctx.stroke();
      }
    }
  }, [dataPoints, swaraFrequencies, currentTime, duration, isLive]);

  return (
    <Paper elevation={3} sx={{ p: 3 }}>
      <Typography variant="h6" gutterBottom>
        Input Frequencies: Frequency vs Time
      </Typography>
      <Box
        sx={{
          width: '100%',
          height: 400,
          border: '1px solid',
          borderColor: 'divider',
          borderRadius: 1,
          backgroundColor: '#ffffff',
          overflow: 'hidden',
        }}
      >
        <canvas
          ref={canvasRef}
          width={1000}
          height={400}
          style={{
            width: '100%',
            height: '100%',
            display: 'block',
          }}
        />
      </Box>
      <Box sx={{ mt: 2, display: 'flex', gap: 2, flexWrap: 'wrap', alignItems: 'center' }}>
        <Typography variant="caption" color="text.secondary">
          {isLive ? 'Live input frequencies (last 10 seconds)' : `Total frequency samples: ${rawFrequencies.length}`}
        </Typography>
        {!isLive && duration > 0 && (
          <Typography variant="caption" color="text.secondary">
            Duration: {duration.toFixed(2)}s | Current: {currentTime.toFixed(2)}s
          </Typography>
        )}
      </Box>
    </Paper>
  );
};

export default WaveformVisualization;

