import React, { useState, useEffect, useRef } from 'react';
import {
  ThemeProvider,
  createTheme,
  CssBaseline,
  Container,
  AppBar,
  Toolbar,
  Typography,
  Box,
  Tabs,
  Tab,
  Grid,
  Paper,
  Link,
  Divider,
} from '@mui/material';
import {
  MusicNote,
  Mic,
  Upload,
  LinkedIn,
} from '@mui/icons-material';

import { AppProvider, useAppContext } from './context/AppContext';
import { AudioProcessor, NOTE_FREQUENCIES } from './utils/audioProcessing';
import { DetectedNote, RawFrequencyData } from './types';

import FileUpload from './components/FileUpload';
import MicrophoneInput from './components/MicrophoneInput';
import NoteVisualization from './components/NoteVisualization';
import TimelineVisualization from './components/TimelineVisualization';
import TonicSelector from './components/TonicSelector';
import ThresholdControls from './components/ThresholdControls';
import FloatingMicrophoneButton from './components/FloatingMicrophoneButton';
import WaveformVisualization from './components/WaveformVisualization';

const theme = createTheme({
  palette: {
    primary: {
      main: '#1976d2',
    },
    secondary: {
      main: '#dc004e',
    },
  },
  typography: {
    fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
  },
});

const MainApp: React.FC = () => {
  const { state, dispatch } = useAppContext();
  const [detectedNotes, setDetectedNotes] = useState<DetectedNote[]>([]);
  const [currentNote, setCurrentNote] = useState<DetectedNote | null>(null);
  const [detectedTonic, setDetectedTonic] = useState<string | null>(null);
  const [isAutoDetecting, setIsAutoDetecting] = useState(false);
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  
  // Raw frequency data for visualization
  const [rawFrequencies, setRawFrequencies] = useState<RawFrequencyData[]>([]);
  const recordingStartTimeRef = useRef<number | null>(null);
  
  // Threshold control state
  const [isGroupingEnabled, setIsGroupingEnabled] = useState(false);
  const [stabilityThreshold, setStabilityThreshold] = useState(100);
  const [transitionThreshold, setTransitionThreshold] = useState(50);
  const [silenceThreshold, setSilenceThreshold] = useState(200);

  const audioProcessorRef = useRef<AudioProcessor | null>(null);
  const audioRef = useRef<HTMLAudioElement | null>(null);

  useEffect(() => {
    audioProcessorRef.current = new AudioProcessor();
    audioProcessorRef.current.setTonic(state.selectedTonic);
    
    audioProcessorRef.current.setOnNoteDetected((note) => {
      setCurrentNote(note);
      if (state.inputMode === 'microphone') {
        setDetectedNotes(prev => [...prev, note]); // Keep all notes
      }
    });

    // Set up raw frequency callback for visualization
    audioProcessorRef.current.setOnRawFrequency((frequency, timestamp) => {
      if (state.inputMode === 'microphone') {
        // Use relative timestamp from recording start
        const relativeTime = recordingStartTimeRef.current 
          ? timestamp - recordingStartTimeRef.current 
          : 0;
        setRawFrequencies(prev => {
          const updated = [...prev, { frequency, timestamp: relativeTime }];
          // Keep only last 10 seconds of data for performance
          if (recordingStartTimeRef.current) {
            const cutoff = relativeTime - 10000; // 10 seconds ago
            return updated.filter(f => f.timestamp >= cutoff);
          }
          return updated;
        });
      }
    });


    return () => {
      if (audioProcessorRef.current) {
        audioProcessorRef.current.stopMicrophoneInput();
      }
    };
  }, [state.inputMode, state.selectedTonic]);

  // Update audio processor when threshold settings change
  useEffect(() => {
    if (audioProcessorRef.current) {
      audioProcessorRef.current.setGroupingEnabled(isGroupingEnabled);
      audioProcessorRef.current.setThresholds(
        stabilityThreshold,
        transitionThreshold,
        silenceThreshold
      );
    }
  }, [isGroupingEnabled, stabilityThreshold, transitionThreshold, silenceThreshold]);

  const handleInputModeChange = (event: React.SyntheticEvent, newValue: number) => {
    const mode: 'file' | 'microphone' = newValue === 0 ? 'microphone' : 'file';
    dispatch({ type: 'SET_INPUT_MODE', payload: mode });
    
    // Stop any ongoing recording when switching modes
    if (audioProcessorRef.current) {
      audioProcessorRef.current.stopMicrophoneInput();
    }
    
    // Reset states
    setDetectedNotes([]);
    setCurrentNote(null);
    setIsPlaying(false);
    setCurrentTime(0);
    setDuration(0);
    setRawFrequencies([]);
  };

  const handleFileSelect = async (file: File) => {
    dispatch({ type: 'SET_FILE_UPLOAD_STATE', payload: { file, isProcessing: true, error: null } });
    
    try {
      // Auto-detect tonic
      setIsAutoDetecting(true);
      const tonic = await audioProcessorRef.current!.detectTonicFromFile(file);
      setDetectedTonic(tonic);
      dispatch({ type: 'SET_SELECTED_TONIC', payload: tonic });
      setIsAutoDetecting(false);

      // Extract continuous pitch track from audio file
      const pitchTrack = await audioProcessorRef.current!.extractContinuousPitchTrack(file);
      setRawFrequencies(pitchTrack);

      // Process the file for note detection
      const notes = await audioProcessorRef.current!.processAudioFile(file);
      setDetectedNotes(notes);
      
      dispatch({ type: 'SET_FILE_UPLOAD_STATE', payload: { isProcessing: false } });
    } catch (error) {
      dispatch({ 
        type: 'SET_FILE_UPLOAD_STATE', 
        payload: { isProcessing: false, error: error instanceof Error ? error.message : 'Unknown error' } 
      });
      setIsAutoDetecting(false);
    }
  };

  const handleFileRemove = () => {
    dispatch({ type: 'SET_FILE_UPLOAD_STATE', payload: { file: null, error: null } });
    setDetectedNotes([]);
    setCurrentNote(null);
    setDetectedTonic(null);
    setIsPlaying(false);
    setCurrentTime(0);
    setDuration(0);
    setRawFrequencies([]);
  };

  const handlePlay = () => {
    if (audioRef.current) {
      audioRef.current.play();
      setIsPlaying(true);
    }
  };

  const handlePause = () => {
    if (audioRef.current) {
      audioRef.current.pause();
      setIsPlaying(false);
    }
  };

  const handleStop = () => {
    if (audioRef.current) {
      audioRef.current.pause();
      audioRef.current.currentTime = 0;
      setIsPlaying(false);
      setCurrentTime(0);
    }
  };

  const handleTimeChange = (time: number) => {
    setCurrentTime(time);
    if (audioRef.current) {
      audioRef.current.currentTime = time;
    }
  };

  const handleStartRecording = async () => {
    try {
      // Reset and start recording
      recordingStartTimeRef.current = Date.now();
      setRawFrequencies([]);
      dispatch({ type: 'SET_AUDIO_INPUT_STATE', payload: { isRecording: true, error: null } });
      await audioProcessorRef.current!.startMicrophoneInput();
    } catch (error) {
      dispatch({ 
        type: 'SET_AUDIO_INPUT_STATE', 
        payload: { 
          isRecording: false, 
          error: error instanceof Error ? error.message : 'Failed to access microphone' 
        } 
      });
    }
  };

  const handleStopRecording = () => {
    audioProcessorRef.current!.stopMicrophoneInput();
    recordingStartTimeRef.current = null;
    dispatch({ type: 'SET_AUDIO_INPUT_STATE', payload: { isRecording: false } });
  };

  const handleRequestPermission = async () => {
    try {
      await navigator.mediaDevices.getUserMedia({ audio: true });
      dispatch({ type: 'SET_AUDIO_INPUT_STATE', payload: { hasPermission: true, error: null } });
    } catch (error) {
      dispatch({ 
        type: 'SET_AUDIO_INPUT_STATE', 
        payload: { 
          hasPermission: false, 
          error: 'Microphone access denied. Please allow microphone access and try again.' 
        } 
      });
    }
  };

  const handleTonicChange = (tonic: string) => {
    dispatch({ type: 'SET_SELECTED_TONIC', payload: tonic });
    if (audioProcessorRef.current) {
      audioProcessorRef.current.setTonic(tonic);
    }
  };

  const handleClearNotes = () => {
    setDetectedNotes([]);
    setCurrentNote(null);
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static">
        <Toolbar>
          <MusicNote sx={{ mr: 2 }} />
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            Swara Detector
          </Typography>
        </Toolbar>
      </AppBar>

      <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
        {/* Notes Display Section - Moved to top */}
        <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
          <Grid container spacing={3}>
            <Grid item xs={12} md={4}>
              <TonicSelector
                selectedTonic={state.selectedTonic}
                detectedTonic={detectedTonic}
                onTonicChange={handleTonicChange}
                isAutoDetecting={isAutoDetecting}
              />
            </Grid>

            <Grid item xs={12} md={8}>
              {state.inputMode === 'microphone' ? (
                <NoteVisualization
                  currentNote={currentNote}
                  detectedNotes={detectedNotes}
                  isLive={true}
                  onClearNotes={handleClearNotes}
                />
              ) : (
                <TimelineVisualization
                  detectedNotes={detectedNotes}
                  currentTime={currentTime}
                  duration={duration}
                  isPlaying={isPlaying}
                  onTimeChange={handleTimeChange}
                  onPlay={handlePlay}
                  onPause={handlePause}
                  onStop={handleStop}
                />
              )}
            </Grid>
          </Grid>
        </Paper>

        {/* Frequency vs Time Visualization Section */}
        {rawFrequencies.length > 0 && (
          <Box sx={{ mb: 3 }}>
            <WaveformVisualization
              rawFrequencies={rawFrequencies}
              tonicFrequency={NOTE_FREQUENCIES[state.selectedTonic] || 440}
              isLive={state.inputMode === 'microphone'}
              currentTime={currentTime}
              duration={duration}
            />
          </Box>
        )}

        {/* Threshold Controls - only show for microphone mode */}
        {state.inputMode === 'microphone' && (
          <ThresholdControls
            isGroupingEnabled={isGroupingEnabled}
            stabilityThreshold={stabilityThreshold}
            transitionThreshold={transitionThreshold}
            silenceThreshold={silenceThreshold}
            onGroupingToggle={setIsGroupingEnabled}
            onStabilityChange={setStabilityThreshold}
            onTransitionChange={setTransitionThreshold}
            onSilenceChange={setSilenceThreshold}
          />
        )}

        {/* Input Controls Section */}
        <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
          <Tabs
            value={state.inputMode === 'microphone' ? 0 : 1}
            onChange={handleInputModeChange}
            centered
            sx={{ mb: 3 }}
          >
            <Tab icon={<Mic />} label="Live Recording" />
            <Tab icon={<Upload />} label="Upload File" />
          </Tabs>

          {state.inputMode === 'file' ? (
            <FileUpload
              file={state.fileUpload.file}
              isUploading={state.fileUpload.isUploading}
              isProcessing={state.fileUpload.isProcessing}
              error={state.fileUpload.error}
              onFileSelect={handleFileSelect}
              onFileRemove={handleFileRemove}
              onPlay={handlePlay}
              onPause={handlePause}
              isPlaying={isPlaying}
            />
          ) : (
            <MicrophoneInput
              isRecording={state.audioInput.isRecording}
              isProcessing={state.audioInput.isProcessing}
              hasPermission={state.audioInput.hasPermission}
              error={state.audioInput.error}
              onStartRecording={handleStartRecording}
              onStopRecording={handleStopRecording}
              onRequestPermission={handleRequestPermission}
            />
          )}
        </Paper>

        {/* Hidden audio element for file playback */}
        {state.fileUpload.file && (
          <audio
            ref={audioRef}
            src={URL.createObjectURL(state.fileUpload.file)}
            onLoadedMetadata={() => {
              if (audioRef.current) {
                setDuration(audioRef.current.duration);
              }
            }}
            onTimeUpdate={() => {
              if (audioRef.current) {
                setCurrentTime(audioRef.current.currentTime);
              }
            }}
            onEnded={() => {
              setIsPlaying(false);
              setCurrentTime(0);
            }}
          />
        )}

        {/* Created By Section */}
        <Box sx={{ mt: 6, mb: 2, textAlign: 'center' }}>
          <Divider sx={{ mb: 2 }} />
          <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>
            Created by{' '}
            <Link
              href="https://www.linkedin.com/in/mnthnx64"
              target="_blank"
              rel="noopener noreferrer"
              sx={{ display: 'inline-flex', alignItems: 'center', gap: 0.5 }}
            >
              Manthan Satish
              <LinkedIn sx={{ fontSize: 16 }} />
            </Link>
            {' '}with the guidance of{' '}
            <Link
              href="https://www.linkedin.com/in/amith-nadig-38a389313/"
              target="_blank"
              rel="noopener noreferrer"
              sx={{ display: 'inline-flex', alignItems: 'center', gap: 0.5 }}
            >
              Amith Nadig
              <LinkedIn sx={{ fontSize: 16 }} />
            </Link>
          </Typography>
        </Box>
      </Container>

      {/* Floating Microphone Button - only show in microphone mode */}
      {state.inputMode === 'microphone' && (
        <FloatingMicrophoneButton
          hasPermission={state.audioInput.hasPermission}
          isRecording={state.audioInput.isRecording}
          onRequestPermission={handleRequestPermission}
          onStartRecording={handleStartRecording}
          onStopRecording={handleStopRecording}
          disabled={state.audioInput.isProcessing}
        />
      )}
    </Box>
  );
};

const App: React.FC = () => {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <AppProvider>
        <MainApp />
      </AppProvider>
    </ThemeProvider>
  );
};

export default App;
