import React from 'react';
import {
  Box,
  Paper,
  Typography,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Chip,
  Alert,
} from '@mui/material';
import { NOTE_FREQUENCIES } from '../utils/audioProcessing';

interface TonicSelectorProps {
  selectedTonic: string;
  detectedTonic: string | null;
  onTonicChange: (tonic: string) => void;
  isAutoDetecting: boolean;
}

const TonicSelector: React.FC<TonicSelectorProps> = ({
  selectedTonic,
  detectedTonic,
  onTonicChange,
  isAutoDetecting,
}) => {
  const noteOptions = Object.keys(NOTE_FREQUENCIES).map(note => ({
    value: note,
    label: note,
    frequency: NOTE_FREQUENCIES[note],
  }));

  return (
    <Paper elevation={3} sx={{ p: 3 }}>
      <Typography variant="h6" gutterBottom>
        Tonic Selection
      </Typography>
      
      <Typography variant="body2" color="text.secondary" paragraph>
        The tonic (Sa) is the fundamental note that all other swaras are measured against.
      </Typography>

      {detectedTonic && (
        <Alert severity="info" sx={{ mb: 2 }}>
          Auto-detected tonic: <strong>{detectedTonic}</strong> ({NOTE_FREQUENCIES[detectedTonic]} Hz)
        </Alert>
      )}

      <FormControl fullWidth>
        <InputLabel id="tonic-select-label">Select Tonic</InputLabel>
        <Select
          labelId="tonic-select-label"
          value={selectedTonic}
          label="Select Tonic"
          onChange={(e) => onTonicChange(e.target.value)}
          disabled={isAutoDetecting}
        >
          {noteOptions.map((note) => (
            <MenuItem key={note.value} value={note.value}>
              <Box display="flex" justifyContent="space-between" width="100%">
                <span>{note.label}</span>
                <Chip
                  label={`${note.frequency} Hz`}
                  size="small"
                  variant="outlined"
                />
              </Box>
            </MenuItem>
          ))}
        </Select>
      </FormControl>

      {selectedTonic && (
        <Box mt={2}>
          <Typography variant="body2" color="text.secondary">
            Selected tonic: <strong>{selectedTonic}</strong> ({NOTE_FREQUENCIES[selectedTonic]} Hz)
          </Typography>
        </Box>
      )}

      {isAutoDetecting && (
        <Box mt={2}>
          <Typography variant="body2" color="primary">
            Auto-detecting tonic from audio...
          </Typography>
        </Box>
      )}
    </Paper>
  );
};

export default TonicSelector;
