import React from 'react';
import {
  Box,
  Paper,
  Typography,
  Switch,
  FormControlLabel,
  Slider,
  Grid,
  Divider,
} from '@mui/material';

interface ThresholdControlsProps {
  isGroupingEnabled: boolean;
  stabilityThreshold: number;
  transitionThreshold: number;
  silenceThreshold: number;
  onGroupingToggle: (enabled: boolean) => void;
  onStabilityChange: (value: number) => void;
  onTransitionChange: (value: number) => void;
  onSilenceChange: (value: number) => void;
}

const ThresholdControls: React.FC<ThresholdControlsProps> = ({
  isGroupingEnabled,
  stabilityThreshold,
  transitionThreshold,
  silenceThreshold,
  onGroupingToggle,
  onStabilityChange,
  onTransitionChange,
  onSilenceChange,
}) => {
  return (
    <Paper elevation={2} sx={{ p: 3, mb: 3 }}>
      <Typography variant="h6" gutterBottom>
        Note Grouping Controls
      </Typography>
      
      <Box mb={3}>
        <FormControlLabel
          control={
            <Switch
              checked={isGroupingEnabled}
              onChange={(e) => onGroupingToggle(e.target.checked)}
              color="primary"
            />
          }
          label={
            <Box>
              <Typography variant="subtitle1">
                Enable Note Grouping
              </Typography>
              <Typography variant="body2" color="text.secondary">
                {isGroupingEnabled 
                  ? 'Notes will be grouped and filtered based on thresholds below'
                  : 'All detected notes will be displayed in real-time'
                }
              </Typography>
            </Box>
          }
        />
      </Box>

      {isGroupingEnabled && (
        <>
          <Divider sx={{ mb: 3 }} />
          
          <Typography variant="subtitle2" gutterBottom color="text.secondary">
            Threshold Settings (in milliseconds)
          </Typography>
          
          <Grid container spacing={3}>
            <Grid item xs={12} md={4}>
              <Box>
                <Typography variant="body2" gutterBottom>
                  Stability Threshold: {stabilityThreshold}ms
                </Typography>
                <Typography variant="caption" color="text.secondary" display="block" gutterBottom>
                  Minimum time a note must be held to be considered stable
                </Typography>
                <Slider
                  value={stabilityThreshold}
                  onChange={(_, value) => onStabilityChange(value as number)}
                  min={10}
                  max={500}
                  step={10}
                  marks={[
                    { value: 50, label: '50ms' },
                    { value: 100, label: '100ms' },
                    { value: 200, label: '200ms' },
                    { value: 500, label: '500ms' },
                  ]}
                  valueLabelDisplay="auto"
                  valueLabelFormat={(value) => `${value}ms`}
                />
              </Box>
            </Grid>
            
            <Grid item xs={12} md={4}>
              <Box>
                <Typography variant="body2" gutterBottom>
                  Transition Threshold: {transitionThreshold}ms
                </Typography>
                <Typography variant="caption" color="text.secondary" display="block" gutterBottom>
                  Minimum time for a new note to be committed after a change
                </Typography>
                <Slider
                  value={transitionThreshold}
                  onChange={(_, value) => onTransitionChange(value as number)}
                  min={10}
                  max={200}
                  step={5}
                  marks={[
                    { value: 25, label: '25ms' },
                    { value: 50, label: '50ms' },
                    { value: 100, label: '100ms' },
                    { value: 200, label: '200ms' },
                  ]}
                  valueLabelDisplay="auto"
                  valueLabelFormat={(value) => `${value}ms`}
                />
              </Box>
            </Grid>
            
            <Grid item xs={12} md={4}>
              <Box>
                <Typography variant="body2" gutterBottom>
                  Silence Threshold: {silenceThreshold}ms
                </Typography>
                <Typography variant="caption" color="text.secondary" display="block" gutterBottom>
                  Minimum silence duration to create a break between notes
                </Typography>
                <Slider
                  value={silenceThreshold}
                  onChange={(_, value) => onSilenceChange(value as number)}
                  min={50}
                  max={1000}
                  step={25}
                  marks={[
                    { value: 100, label: '100ms' },
                    { value: 200, label: '200ms' },
                    { value: 500, label: '500ms' },
                    { value: 1000, label: '1000ms' },
                  ]}
                  valueLabelDisplay="auto"
                  valueLabelFormat={(value) => `${value}ms`}
                />
              </Box>
            </Grid>
          </Grid>
          
          <Box mt={2} p={2} sx={{ backgroundColor: 'grey.50', borderRadius: 1 }}>
            <Typography variant="body2" color="text.secondary">
              <strong>How it works:</strong> When grouping is enabled, the system will wait for notes to be stable 
              for the specified duration before displaying them. This helps reduce noise and provides cleaner output, 
              but may miss very quick transitions. Disable grouping for real-time detection of all notes.
            </Typography>
          </Box>
        </>
      )}
    </Paper>
  );
};

export default ThresholdControls;
