import React, { useRef, useEffect } from 'react';
import {
  Box,
  Paper,
  Typography,
  Chip,
  LinearProgress,
  TextField,
  Button,
  IconButton,
} from '@mui/material';
import { Clear } from '@mui/icons-material';
import { DetectedNote } from '../types';

interface NoteVisualizationProps {
  currentNote: DetectedNote | null;
  detectedNotes: DetectedNote[];
  isLive: boolean;
  onClearNotes?: () => void;
}

const NoteVisualization: React.FC<NoteVisualizationProps> = ({
  currentNote,
  detectedNotes,
  isLive,
  onClearNotes,
}) => {
  const notesScrollRef = useRef<HTMLDivElement>(null);

  // Auto-scroll to bottom when new notes are detected
  useEffect(() => {
    if (notesScrollRef.current && detectedNotes.length > 0) {
      notesScrollRef.current.scrollTop = notesScrollRef.current.scrollHeight;
    }
  }, [detectedNotes]);
  const getConfidenceColor = (confidence: number) => {
    if (confidence >= 0.8) return 'success';
    if (confidence >= 0.6) return 'warning';
    return 'error';
  };

  const getConfidenceLabel = (confidence: number) => {
    if (confidence >= 0.8) return 'High';
    if (confidence >= 0.6) return 'Medium';
    return 'Low';
  };

  const formatNotesSequence = (notes: DetectedNote[]): string => {
    if (notes.length === 0) return '';
    
    let result = '';
    let lastNoteName = '';
    let lastNoteTimestamp = 0;
    
    for (let i = 0; i < notes.length; i++) {
      const note = notes[i];
      const noteName = note.swara.symbol;
      
      // Check if this is a break (different note or first note)
      if (i === 0 || noteName !== lastNoteName) {
        // Add dots for break if not the first note and break is long enough
        if (i > 0) {
          const breakDuration = note.timestamp - lastNoteTimestamp;
          const dots = getDotsForDuration(breakDuration);
          if (dots) { // Only add dots if there are any (no dots for quick transitions)
            result += `${dots} `;
          }
        }
        
        lastNoteName = noteName;
        lastNoteTimestamp = note.timestamp;
        
        // Add the note with 2nd character in subscript (if it exists)
        result += formatNoteWithSubscript(noteName);
      } else {
        // Same note, just add it again without counting
        lastNoteTimestamp = note.timestamp;
        result += formatNoteWithSubscript(noteName);
      }
    }
    
    return result;
  };

  const getDotsForDuration = (durationMs: number): string => {
    // Convert duration to dots based on time intervals
    // 0-500ms: no dots (quick transitions)
    // 500-1000ms: 2 dots (..)
    // 1000-2000ms: 3 dots (...)
    // 2000-3000ms: 4 dots (....)
    // 3000ms+: 5 dots (.....)
    
    if (durationMs < 500) return ''; // No dots for quick transitions
    if (durationMs < 1000) return '..';
    if (durationMs < 2000) return '...';
    if (durationMs < 3000) return '....';
    return '.....';
  };

  const formatNoteWithSubscript = (noteSymbol: string): string => {
    // If the note symbol has a 2nd character (like R1, R2, G2, etc.), show it in subscript
    if (noteSymbol.length > 1) {
      const firstChar = noteSymbol[0];
      const secondChar = noteSymbol[1];
      return `${firstChar}${getSmallSubscript(secondChar)}`;
    }
    // If no 2nd character (like S, P), just return the symbol
    return noteSymbol;
  };

  const getSmallSubscript = (char: string): string => {
    // Use smaller subscript characters for better space efficiency
    const smallSubscripts: { [key: string]: string } = {
      '1': '', '2': '', '3': '', '4': '', '5': '',
      '6': '', '7': '', '8': '', '9': '', '0': ''
    };
    return smallSubscripts[char] || char;
  };

  return (
    <Paper elevation={3} sx={{ p: 3 }}>
      <Typography variant="h6" gutterBottom>
        {isLive ? 'Live Note Detection' : 'Detected Notes'}
      </Typography>

      {currentNote && (
        <Box 
          mb={3} 
          p={3} 
          sx={{ 
            backgroundColor: 'primary.light', 
            borderRadius: 3,
            border: '2px solid',
            borderColor: 'primary.main',
            textAlign: 'center',
          }}
        >
          <Typography 
            variant="h2" 
            component="div" 
            gutterBottom
            sx={{ 
              fontWeight: 'bold',
              color: 'primary.contrastText',
              textShadow: '2px 2px 4px rgba(0,0,0,0.3)',
            }}
          >
            {currentNote.swara.symbol}
          </Typography>
          
          <Typography 
            variant="h5" 
            gutterBottom
            sx={{ 
              color: 'primary.contrastText',
              fontWeight: 500,
            }}
          >
            {currentNote.swara.name}
          </Typography>
          
          <Box display="flex" justifyContent="center" gap={2} mb={2} flexWrap="wrap">
            <Chip
              label={`${currentNote.frequency.toFixed(1)} Hz`}
              variant="filled"
              size="medium"
              sx={{ 
                backgroundColor: 'rgba(255,255,255,0.2)',
                color: 'primary.contrastText',
                fontWeight: 'bold',
              }}
            />
            <Chip
              label={`${(currentNote.confidence * 100).toFixed(0)}% confidence`}
              color={getConfidenceColor(currentNote.confidence)}
              size="medium"
              sx={{ fontWeight: 'bold' }}
            />
          </Box>
          
          <LinearProgress
            variant="determinate"
            value={currentNote.confidence * 100}
            color={getConfidenceColor(currentNote.confidence)}
            sx={{ 
              height: 12, 
              borderRadius: 6,
              backgroundColor: 'rgba(255,255,255,0.3)',
              '& .MuiLinearProgress-bar': {
                borderRadius: 6,
              }
            }}
          />
        </Box>
      )}

      {detectedNotes.length > 0 && (
        <Box>
          <Box display="flex" justifyContent="space-between" alignItems="center" mb={1}>
            <Typography variant="subtitle1">
              Detected Sequence ({detectedNotes.length} notes)
            </Typography>
            {onClearNotes && (
              <Button
                variant="outlined"
                size="small"
                startIcon={<Clear />}
                onClick={onClearNotes}
                sx={{
                  minWidth: 'auto',
                  px: 1.5,
                  py: 0.5,
                }}
              >
                Clear
              </Button>
            )}
          </Box>
          
          {/* Scrollable text field with notes and breaks */}
          <Box
            ref={notesScrollRef}
            sx={{
              maxHeight: 200,
              overflowY: 'auto',
              border: '1px solid',
              borderColor: 'divider',
              borderRadius: 1,
              backgroundColor: 'grey.50',
              '&::-webkit-scrollbar': {
                width: '8px',
              },
              '&::-webkit-scrollbar-track': {
                backgroundColor: 'grey.200',
                borderRadius: '4px',
              },
              '&::-webkit-scrollbar-thumb': {
                backgroundColor: 'grey.400',
                borderRadius: '4px',
                '&:hover': {
                  backgroundColor: 'grey.500',
                },
              },
            }}
          >
            <TextField
              multiline
              fullWidth
              variant="outlined"
              value={formatNotesSequence(detectedNotes)}
              InputProps={{
                readOnly: true,
                sx: {
                  fontSize: '1.2rem',
                  fontFamily: 'monospace',
                  '& .MuiInputBase-input': {
                    padding: 2,
                    lineHeight: 1.8,
                    border: 'none',
                    '&:focus': {
                      outline: 'none',
                    },
                  },
                  '& .MuiOutlinedInput-notchedOutline': {
                    border: 'none',
                  },
                },
              }}
              sx={{
                '& .MuiOutlinedInput-root': {
                  backgroundColor: 'transparent',
                  minHeight: 120,
                },
              }}
            />
          </Box>
          
          {/* Summary */}
          <Box mt={2} p={2} sx={{ backgroundColor: 'grey.50', borderRadius: 1 }}>
            <Typography variant="body2" color="text.secondary">
              <strong>Latest:</strong> {detectedNotes[detectedNotes.length - 1]?.swara.name || 'None'} 
              {'  '}
              <strong>Total:</strong> {detectedNotes.length} notes detected
              {isLive && (
                <>
                  {'  '}
                  <strong>Live:</strong> {currentNote ? 'Detecting' : 'Listening...'}
                </>
              )}
            </Typography>
          </Box>
        </Box>
      )}

      {!currentNote && detectedNotes.length === 0 && (
        <Box textAlign="center" py={4}>
          <Typography variant="body1" color="text.secondary">
            {isLive
              ? 'Start recording to see live note detection'
              : 'Upload an audio file to see detected notes'}
          </Typography>
        </Box>
      )}
    </Paper>
  );
};

export default NoteVisualization;
