import React from 'react';
import {
  Box,
  Paper,
  Typography,
  Button,
  IconButton,
  Alert,
  CircularProgress,
} from '@mui/material';
import {
  Mic,
  MicOff,
  Stop,
  PlayArrow,
} from '@mui/icons-material';

interface MicrophoneInputProps {
  isRecording: boolean;
  isProcessing: boolean;
  hasPermission: boolean;
  error: string | null;
  onStartRecording: () => void;
  onStopRecording: () => void;
  onRequestPermission: () => void;
}

const MicrophoneInput: React.FC<MicrophoneInputProps> = ({
  isRecording,
  isProcessing,
  hasPermission,
  error,
  onStartRecording,
  onStopRecording,
  onRequestPermission,
}) => {
  if (!hasPermission) {
    return (
      <Paper elevation={3} sx={{ p: 2, textAlign: 'center' }}>
        <Box display="flex" alignItems="center" justifyContent="center" gap={2} mb={2}>
          <Mic sx={{ fontSize: 32, color: 'grey.400' }} />
          <Typography variant="h6">
            Enable Microphone Access
          </Typography>
        </Box>
        
        <Typography variant="body2" color="text.secondary" paragraph>
          Allow microphone access to start live audio analysis.
        </Typography>

        <Button
          variant="contained"
          size="medium"
          startIcon={<Mic />}
          onClick={onRequestPermission}
          sx={{
            px: 3,
            py: 1,
          }}
        >
          Allow Access
        </Button>

        {error && (
          <Alert severity="error" sx={{ mt: 2 }}>
            {error}
          </Alert>
        )}
      </Paper>
    );
  }

  return (
    <Paper
      elevation={3}
      sx={{
        p: 2,
        textAlign: 'center',
        backgroundColor: isRecording ? 'error.light' : 'primary.light',
        color: isRecording ? 'error.contrastText' : 'primary.contrastText',
        transition: 'all 0.3s ease-in-out',
      }}
    >
      <Box display="flex" flexDirection="column" alignItems="center" gap={1.5}>
        {isRecording ? (
          <>
            <Box position="relative">
              <CircularProgress
                size={48}
                thickness={4}
                sx={{
                  color: 'inherit',
                  animation: 'pulse 1.5s ease-in-out infinite',
                }}
              />
              <Mic
                sx={{
                  position: 'absolute',
                  top: '50%',
                  left: '50%',
                  transform: 'translate(-50%, -50%)',
                  fontSize: 24,
                }}
              />
            </Box>
            
            <Typography variant="subtitle1">
              Recording... Sing or play
            </Typography>
            
            <Button
              variant="contained"
              size="medium"
              startIcon={<Stop />}
              onClick={onStopRecording}
              sx={{
                backgroundColor: 'error.main',
                '&:hover': {
                  backgroundColor: 'error.dark',
                },
              }}
            >
              Stop Recording
            </Button>
          </>
        ) : (
          <>
            <Box display="flex" alignItems="center" gap={1.5}>
              <Mic sx={{ fontSize: 32 }} />
              <Typography variant="subtitle1">
                Ready to analyze live audio
              </Typography>
            </Box>
            
            <Button
              variant="contained"
              size="medium"
              startIcon={<PlayArrow />}
              onClick={onStartRecording}
              disabled={isProcessing}
              sx={{
                backgroundColor: 'primary.main',
                '&:hover': {
                  backgroundColor: 'primary.dark',
                },
              }}
            >
              Start Recording
            </Button>
          </>
        )}

        {isProcessing && (
          <Box display="flex" alignItems="center" gap={1}>
            <CircularProgress size={20} />
            <Typography variant="body2">
              Processing...
            </Typography>
          </Box>
        )}

        {error && (
          <Alert severity="error" sx={{ mt: 1 }}>
            {error}
          </Alert>
        )}
      </Box>
    </Paper>
  );
};

export default MicrophoneInput;
