import React from 'react';
import {
  Fab,
  Tooltip,
  Zoom,
} from '@mui/material';
import {
  Mic,
  MicOff,
} from '@mui/icons-material';

interface FloatingMicrophoneButtonProps {
  hasPermission: boolean;
  isRecording: boolean;
  onRequestPermission: () => void;
  onStartRecording: () => void;
  onStopRecording: () => void;
  disabled?: boolean;
}

const FloatingMicrophoneButton: React.FC<FloatingMicrophoneButtonProps> = ({
  hasPermission,
  isRecording,
  onRequestPermission,
  onStartRecording,
  onStopRecording,
  disabled = false,
}) => {
  const handleClick = () => {
    if (!hasPermission) {
      onRequestPermission();
    } else if (isRecording) {
      onStopRecording();
    } else {
      onStartRecording();
    }
  };

  const getTooltipText = () => {
    if (!hasPermission) {
      return 'Allow Microphone Access';
    } else if (isRecording) {
      return 'Stop Recording';
    } else {
      return 'Start Recording';
    }
  };

  const getIcon = () => {
    if (!hasPermission) {
      return <MicOff />;
    } else if (isRecording) {
      return <MicOff />;
    } else {
      return <Mic />;
    }
  };

  const getColor = () => {
    if (!hasPermission) {
      return 'default';
    } else if (isRecording) {
      return 'error';
    } else {
      return 'primary';
    }
  };

  return (
    <Zoom in={true}>
      <Tooltip title={getTooltipText()} placement="left">
        <Fab
          color={getColor()}
          aria-label="microphone"
          onClick={handleClick}
          disabled={disabled}
          sx={{
            position: 'fixed',
            bottom: 24,
            right: 24,
            zIndex: 1000,
            boxShadow: 3,
            '&:hover': {
              boxShadow: 6,
            },
            // Add pulsing animation when recording
            ...(isRecording && {
              animation: 'pulse 1.5s ease-in-out infinite',
              '@keyframes pulse': {
                '0%': {
                  transform: 'scale(1)',
                },
                '50%': {
                  transform: 'scale(1.1)',
                },
                '100%': {
                  transform: 'scale(1)',
                },
              },
            }),
          }}
        >
          {getIcon()}
        </Fab>
      </Tooltip>
    </Zoom>
  );
};

export default FloatingMicrophoneButton;
