import React from 'react';
import {
  Box,
  Paper,
  Typography,
  Slider,
  IconButton,
  Chip,
} from '@mui/material';
import {
  PlayArrow,
  Pause,
  Stop,
} from '@mui/icons-material';
import { DetectedNote } from '../types';

interface TimelineVisualizationProps {
  detectedNotes: DetectedNote[];
  currentTime: number;
  duration: number;
  isPlaying: boolean;
  onTimeChange: (time: number) => void;
  onPlay: () => void;
  onPause: () => void;
  onStop: () => void;
}

const TimelineVisualization: React.FC<TimelineVisualizationProps> = ({
  detectedNotes,
  currentTime,
  duration,
  isPlaying,
  onTimeChange,
  onPlay,
  onPause,
  onStop,
}) => {
  const formatTime = (time: number) => {
    const minutes = Math.floor(time / 60);
    const seconds = Math.floor(time % 60);
    return `${minutes}:${seconds.toString().padStart(2, '0')}`;
  };

  const getCurrentNote = (): DetectedNote | null => {
    if (detectedNotes.length === 0) return null;
    
    // Find the note closest to current time
    const currentNote = detectedNotes.reduce((closest, note) => {
      const closestDiff = Math.abs(closest.timestamp - currentTime);
      const noteDiff = Math.abs(note.timestamp - currentTime);
      return noteDiff < closestDiff ? note : closest;
    });
    
    return currentNote;
  };

  const currentNote = getCurrentNote();

  return (
    <Paper elevation={3} sx={{ p: 3 }}>
      <Typography variant="h6" gutterBottom>
        Timeline Analysis
      </Typography>

      {detectedNotes.length > 0 ? (
        <>
          {/* Current Note Display */}
          {currentNote && (
            <Box mb={3} textAlign="center">
              <Typography variant="h4" component="div" gutterBottom>
                {currentNote.swara.symbol}
              </Typography>
              <Typography variant="h6" color="text.secondary" gutterBottom>
                {currentNote.swara.name}
              </Typography>
              <Box display="flex" justifyContent="center" gap={1}>
                <Chip
                  label={`${currentNote.frequency.toFixed(1)} Hz`}
                  variant="outlined"
                  size="small"
                />
                <Chip
                  label={`${(currentNote.confidence * 100).toFixed(0)}%`}
                  color={currentNote.confidence >= 0.8 ? 'success' : 'warning'}
                  size="small"
                />
              </Box>
            </Box>
          )}

          {/* Timeline Slider */}
          <Box mb={2}>
            <Slider
              value={currentTime}
              min={0}
              max={duration}
              step={0.1}
              onChange={(_, value) => onTimeChange(value as number)}
              valueLabelDisplay="auto"
              valueLabelFormat={formatTime}
              sx={{ mb: 1 }}
            />
            <Box display="flex" justifyContent="space-between">
              <Typography variant="caption" color="text.secondary">
                {formatTime(currentTime)}
              </Typography>
              <Typography variant="caption" color="text.secondary">
                {formatTime(duration)}
              </Typography>
            </Box>
          </Box>

          {/* Playback Controls */}
          <Box display="flex" justifyContent="center" gap={1} mb={3}>
            <IconButton
              onClick={isPlaying ? onPause : onPlay}
              color="primary"
              size="large"
            >
              {isPlaying ? <Pause /> : <PlayArrow />}
            </IconButton>
            <IconButton onClick={onStop} color="primary">
              <Stop />
            </IconButton>
          </Box>

          {/* Notes Overview */}
          <Box>
            <Typography variant="subtitle1" gutterBottom>
              Detected Notes ({detectedNotes.length})
            </Typography>
            <Box
              sx={{
                height: 60,
                border: '1px solid',
                borderColor: 'divider',
                borderRadius: 1,
                p: 1,
                overflow: 'hidden',
                position: 'relative',
              }}
            >
              {detectedNotes.map((note, index) => {
                const position = (note.timestamp / duration) * 100;
                const width = Math.max(2, (100 / detectedNotes.length) * 0.5);
                
                return (
                  <Box
                    key={index}
                    sx={{
                      position: 'absolute',
                      left: `${position}%`,
                      top: '50%',
                      transform: 'translateY(-50%)',
                      width: `${width}%`,
                      height: '80%',
                      backgroundColor: note.confidence >= 0.8 ? 'success.main' : 'warning.main',
                      borderRadius: 0.5,
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      fontSize: '0.7rem',
                      color: 'white',
                      fontWeight: 'bold',
                    }}
                    title={`${note.swara.symbol} - ${formatTime(note.timestamp / 1000)}`}
                  >
                    {note.swara.symbol}
                  </Box>
                );
              })}
            </Box>
          </Box>
        </>
      ) : (
        <Box textAlign="center" py={4}>
          <Typography variant="body1" color="text.secondary">
            No notes detected. Upload an audio file to see the timeline analysis.
          </Typography>
        </Box>
      )}
    </Paper>
  );
};

export default TimelineVisualization;
